import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SearchStudentsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getStudentsByEduCompId(EduCompId: any, data: any) {
    return this.post(`Admin/getStudents/${EduCompId}`, data);
  }

  getStudentBalance(userId: any, EduCompId: any) {
    return this.get(`Admin/returnBalanceCheck/${userId}/${EduCompId}`);
  }

  getStudentPayments<T>(EduCompId: any, page: number, userId: any) {
    return this.get<T>(
      `Admin/returnUserPayments/${EduCompId}/${page}/${userId}`
    );
  }

  getStudentPaymentsHistory<T>(EduCompId: any, page: number, userId: any) {
    return this.get<T>(
      `Admin/returnUserPaymentsHistory/${EduCompId}/${page}/${userId}`
    );
  }

  getStudentStatus(userId: any, EduCompId: any) {
    return this.get(`Admin/educationalCompStatus/${userId}/${EduCompId}`);
  }

  deleteStudent(EduCompId: number, id: any) {
    return this.post(
      `Admin/RemoveEducationalComponent_User/${EduCompId}/${id}`,
      null
    );
  }

  getStudentGroupsAndStatuts(EduCompId: number) {
    return this.get(`Admin/JoinStudentsEducationalComponent/${EduCompId}`);
  }

  getStudentStatusList(page: number, EduCompUserId: any) {
    return this.get(`Admin/ReservationStatusPaging/${page}/${EduCompUserId}`);
  }

  updateStudentStatus(EduCompId: number, body: any) {
    return this.post(`Admin/EduCompStatus/${EduCompId}`, body);
  }

  getUserDataRefundLecture(userId: any) {
    return this.get(`Admin/RefundLecture/${userId}`);
  }

  refundLecture(body: any) {
    return this.post("Admin/RefundLecture", body);
  }

  getStudentCode(EduCompId: number, userId: number) {
    return this.get(`Admin/ChangeStudentCode/${EduCompId}/${userId}`);
  }

  updateStudentCode(EduCompId: number, body: any) {
    return this.post(`Admin/ChangeStudentCode/${EduCompId}`, body);
  }
}
