import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { unit } from "app/admin/models/admin/educations";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { LanguageService } from "app/shared/services/language.service";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "app-course-units-dropdown",
  templateUrl: "./course-units-dropdown.component.html",
  styleUrls: ["./course-units-dropdown.component.scss"],
})
export class CourseUnitsDropdownComponent implements OnInit, OnChanges {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() label: string = "part";
  @Input() courseId: number;
  @Input() selectFirstItem: boolean = false;
  @Input() units: unit[] = [];
  @Input() listOutside: boolean = false;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  @Output() selectChange = new EventEmitter<number>();

  constructor(
    private subjectsService: SubjectsService,
    public languageService: LanguageService
  ) {}

  ngOnInit() {
    // this.control.valueChanges.subscribe((unitId) =>
    //   this.selectChange.emit(unitId)
    // );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.courseId?.currentValue != changes.courseId?.previousValue &&
      !this.listOutside
    )
      changes.courseId.currentValue > 0
        ? this.getAllStagesByCourseId(changes.courseId.currentValue)
        : (this.units = []);
  }

  getAllStagesByCourseId(courseId) {
    this.subjectsService.getUnits(courseId).subscribe((res) => {
      if (res.length > 0) {
        this.units = res;
        if (this.selectFirstItem) this.control.setValue(this.units[0].id);
      }
    });
  }

  onChange(value: number) {
    this.selectChange.emit(value);
  }
}
