import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {LanguageService} from "app/shared/services/language.service";
import {UploadFileService} from "app/shared/services/General/upload-file.service";
import {AppAssets} from "app/shared/data/app-assets";
import {giftGalleryViewModel, giftViewModel} from "app/admin/models/admin/gifts";
import {BuyGiftDialogComponent} from "app/student/pages/gifts/buy-gift-dialog/buy-gift-dialog.component";
import {MatDialog} from "@angular/material/dialog";

@Component({
  selector: 'app-gift-card',
  templateUrl: './gift-card.component.html',
  styleUrls: ['./gift-card.component.scss']
})
export class GiftCardComponent implements OnInit {
  @Input() gift: giftGalleryViewModel; //| giftViewModel
  @Input() availablePoints: number;
  @Input() isAdmin: boolean = false;
  @Output() reload = new EventEmitter<boolean>();

  constructor(public languageService: LanguageService,
              public fileService: UploadFileService,
              public appAssets: AppAssets,
              private dialog: MatDialog) {
  }

  ngOnInit(): void {
  }


  openBuyDialog(gift: giftGalleryViewModel) {
    this.dialog
      .open(BuyGiftDialogComponent, {
        data: {
          gift: gift,
        },
        maxWidth: "700px",
        minWidth: "50vw",
        autoFocus: false,
        panelClass: "dialog-radius",
      })
      .afterClosed()
      .subscribe((hasUpdate) => {
        if (hasUpdate) {
          this.reload.emit(hasUpdate);
          // this.getGiftGallery();
          // this.giftServ.getPoints();
        }
      });
  }

}
