import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IAddCourseRequest } from "app/admin/models/admin/course-request";
import {
  IMyCoursesMV,
  IMyDiplomasMV,
} from "app/admin/models/admin/setup/course";
import { CourseAndTeachingRequestService } from "app/admin/services/Admin/course-teaching-request.service";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-add-course-request-details",
  templateUrl: "./add-course-request-details.component.html",
  styleUrls: ["./add-course-request-details.component.scss"],
})
export class AddCourseRequestDetailsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  course: IMyCoursesMV;
  diploma: IMyDiplomasMV;
  isDone: boolean = false;

  constructor(
    public languageService: LanguageService,
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    private courseAndTeachingRequestService: CourseAndTeachingRequestService,
    public dialogRef: MatDialogRef<AddCourseRequestDetailsComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: { course: IMyCoursesMV; diploma: IMyDiplomasMV }
  ) {
    this.course = data.course;
    this.diploma = data.diploma;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      reason: [null, [Validators.required, CustomeValidator.whiteSpace]],
      durationExpected: [null, [Validators.required, CustomeValidator.bigZero]],
    });
  }

  get formCtrl() {
    return this.myForm.controls;
  }

  addRequestDetails() {
    this.submitted = true;
    // console.log(this.myForm.value);
    if (this.myForm.valid) {
      let addRequestDetails: IAddCourseRequest = {
        courseId: this.course?.subject_id ?? null,
        diploma_id: this.diploma?.id ?? null,
        reasonOfRequest: this.formCtrl?.reason.value,
        numberOfDays: this.formCtrl?.durationExpected?.value,
      };

      this.AddLearningRequest(addRequestDetails);
    }
  }

  AddLearningRequest(addDetails: IAddCourseRequest) {
    this.spinner.show();
    this.courseAndTeachingRequestService
      .addCourseRequest(addDetails)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.isDone = true;
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }
}
