import { AuthService } from "app/shared/services/auth/auth.service";
import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IDepartmentModel } from "app/admin/models/setup/department";
import { DepartmentService } from "app/admin/services/setup/department.service";
import { LanguageService } from "app/shared/services/language.service";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "department-dropdown",
  templateUrl: "./department-dropdown.component.html",
  styleUrls: ["./department-dropdown.component.scss"],
})
export class DepartmentDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  EduCompId: number;
  departmentList: IDepartmentModel[];

  constructor(
    private departmentService: DepartmentService,
    public languageService: LanguageService,
    private authService: AuthService
  ) {
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllDepartments();
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.control.setValue(null);
      this.getAllDepartments();
    });
  }

  getAllDepartments() {
    this.departmentService
      .getDepartmentsDropDown<IDepartmentModel[]>(this.EduCompId)
      .subscribe((res) => {
        if (res) this.departmentList = res;
      });
  }
}
