import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { IJobTitleSearchVM } from "app/admin/models/setup/job-title";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JobTitleService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllJobTitles(data: IJobTitleSearchVM, eduCompId: number, page: number) {
    return this.post(`jobTitle/getAllJobTitle/${eduCompId}/${page}`, data);
  }

  addJobTitle<T>(jobTitle: T) {
    return this.post<T>(`jobTitle/PostJobTitle`, jobTitle);
  }

  updateJobTitles<T>(jobTitle: T, id: number) {
    return this.put<T>(`jobTitle/PutJobTitle/${id}`, jobTitle);
  }

  deleteJobTitles(id: number) {
    return this.delete(`jobTitle/DeleteJobTitle/${id}`);
  }

  getJobTitleByDepartment<T>(id: number) {
    return this.get<T>(`jobTitle/getJobTitlesByDepartmentId/${id}`);
  }

  getSpecificJobTitlesByDepartmentIds(ids: any) {
    return this.post(`jobTitle/getSpecificJobTitlesByDepartmentIds`, ids);
  }

  deleteJobTitleDepartment(id: number) {
    return this.delete(`jobTitle/DeleteDepartmentOfJobTitle/${id}`);
  }

  update_JobTitle_Department<T>(body: T, id: number) {
    return this.put(`jobTitle/EditDepartmentOfJobTitle/${id}`, body);
  }
}
