import {
    AfterViewInit,
    ChangeDetectorRef,
    Component,
    ElementRef,
    HostListener,
    OnDestroy,
    OnInit,
    ViewChild,
} from "@angular/core";
import {TranslateService} from "@ngx-translate/core";
import {customAnimations} from "../animations/custom-animations";
import {DeviceDetectorService} from "ngx-device-detector";
import {ConfigService} from "../services/config.service";
import {Subscription} from "rxjs";
import {LayoutService} from "../services/layout.service";
import {AuthService} from "../services/auth/auth.service";
import {GeneralService} from "../services/General/general.service";
import {IBranchesVM, IEducationalCompsVM, IUserVM, UserModel} from "../models/general/general";
import {GiftsService} from "app/admin/services/Admin/gifts.service";
import {IUserPointsVM} from "app/admin/models/admin/gifts";
import {UserFunctionsService} from "../services/auth/user-functions.service";
import {AppAssets} from "app/shared/data/app-assets";
import {AppStrings} from "app/shared/data/app-strings";
import {UploadFileService} from "app/shared/services/General/upload-file.service";
import {getSvgPath} from "app/shared/data/svg-manager";
import {LanguageService} from "app/shared/services/language.service";

@Component({
    selector: "app-sidebar",
    templateUrl: "./vertical-menu.component.html",
    animations: customAnimations,
})
export class VerticalMenuComponent implements OnInit, AfterViewInit, OnDestroy {
    @ViewChild("toggleIcon") toggleIcon: ElementRef;
    public menuItems: any[];
    level: number = 0;
    logoUrl = "assets/img/logo.png";
    logo = "assets/img/elements/b1.png";
    public config: any = {};
    protected innerWidth: any;
    layoutSub: Subscription;
    configSub: Subscription;
    perfectScrollbarEnable = true;
    collapseSidebar = false;
    resizeTimeout;
    points: number;
    EducationalComps: IEducationalCompsVM[];
    Branches: IBranchesVM[];
    EduCompId: any;
    branchId: number;
    menuPosition: string
    user: IUserVM;
    picturePath: string;

    constructor(
        public translate: TranslateService,
        private layoutService: LayoutService,
        private configService: ConfigService,
        private cdr: ChangeDetectorRef,
        private deviceService: DeviceDetectorService,
        public userFunctionsService: UserFunctionsService,
        public authService: AuthService,
        private GeneralServ: GeneralService,
        private giftServ: GiftsService,
        public assets: AppAssets,
        public appStrings: AppStrings,
        public fileService: UploadFileService,
        public lang: LanguageService
    ) {
        this.config = this.configService.templateConf;
        this.innerWidth = window.innerWidth;
        this.isTouchDevice();
        this.EduCompId = this.authService.getEduCompId();
        if (this.EduCompId) this.onChangeEduCompId(this.EduCompId);
        this.branchId = Number(this.authService.getBranchId());
        if (!this.authService.isStudent()) this.menuPosition = "Side"; else this.menuPosition = "Top";

        this.GeneralServ.userData.subscribe((user: UserModel) => {
            this.user = user?.user;
            this.picturePath = user?.picturePath;
        });
        console.log(getSvgPath('nav_categories'))
    }

    ngOnInit() {
        if (this.authService.isStudent()) {
            this.giftServ.getPoints();
        }
        this.giftServ?.userPoints.subscribe((point: IUserPointsVM) => {
            this.points = point.UnUsedpoints;
        });
        if (
            this.authService.isUserLoggedIn() &&
            (!this.authService.isStudent() || !this.authService.isSupport())
        ) {
            this.getEduCompByUser();
        }
        if (this.authService.isUserLoggedIn()) {
            this.userFunctionsService.getUserFunctions(this.branchId);
            this.userFunctionsService.menuItems.subscribe((menu) => {
                this.menuItems = menu;
                // console.log("vertical-menu:", menu);
            });
        }
        //  this.menuItems = ROUTES;
    }

    logout() {
        this.authService.logout();
    }

    ngAfterViewInit() {
        this.authService.branchId.subscribe((e) => {
            this.branchId = e.branchId;
            if (
                this.authService.isUserLoggedIn() &&
                (!this.authService.isStudent() || !this.authService.isSupport())
            ) {
                this.userFunctionsService.getUserFunctions(this.branchId);
            }
        });

        this.authService.EduCompId.subscribe((e) => {
            this.EduCompId = e.EduCompId;
            if (
                this.authService.isUserLoggedIn() &&
                (!this.authService.isStudent() || !this.authService.isSupport())
            ) {
                this.userFunctionsService.getUserFunctions(this.branchId);
                //  this.onChangeEduCompId(e.EduCompId);
            }
        });

        this.configSub = this.configService.templateConf$.subscribe(
            (templateConf) => {
                if (templateConf) {
                    this.config = templateConf;
                }
                this.loadLayout();
                this.cdr.markForCheck();
            }
        );

        this.layoutSub = this.layoutService.overlaySidebarToggle$.subscribe(
            (collapse) => {
                if (this.config.layout.menuPosition === "Side") {
                    this.collapseSidebar = collapse;
                }
            }
        );
    }

    @HostListener("window:resize", ["$event"])
    onWindowResize(event) {
        if (this.resizeTimeout) {
            clearTimeout(this.resizeTimeout);
        }
        this.resizeTimeout = setTimeout(
            (() => {
                this.innerWidth = event.target.innerWidth;
                this.loadLayout();
            }).bind(this),
            500
        );
    }

    getEduCompByUser() {
        this.Branches = [];
        this.branchId = null;
        this.GeneralServ.getEduCompByUser<IEducationalCompsVM>().subscribe(
            (res: any) => {
                this.EducationalComps = res;
            }
        );
    }

    onChangeEduCompId(EduCompId: any) {
        this.authService.setEduCompId(EduCompId);
        if (EduCompId !== "null" && EduCompId != "undefined") {
            this.GeneralServ.getBranchesByEduComp<IBranchesVM>(EduCompId).subscribe(
                (res: any) => {
                    this.Branches = res;
                    this.authService.setBranchId(res[0]?.Id);
                    this.branchId = res[0]?.Id;
                }
            );
        }
    }

    changebranchId() {
        this.authService.setBranchId(this.branchId);
    }

    loadLayout() {
        if (this.config.layout.menuPosition === "Top") {
            // Horizontal Menu
            if (this.innerWidth < 1200) {
                // Screen size < 1200
                //  this.menuItems = HROUTES;
            }
        } else if (this.config.layout.menuPosition === "Side") {
            // Vertical Menu{
            // this.menuItems = ROUTES;
        }

        if (this.config.layout.sidebar.backgroundColor === "white") {
            this.logoUrl = "assets/img/logo-dark.png";
        } else {
            this.logoUrl = "assets/img/logo.png";
        }

        if (this.config.layout.sidebar.collapsed) {
            this.collapseSidebar = true;
        } else {
            this.collapseSidebar = false;
        }
    }

    toggleSidebar() {
        let conf = this.config;
        conf.layout.sidebar.collapsed = !this.config.layout.sidebar.collapsed;
        this.configService.applyTemplateConfigChange({layout: conf.layout});

        setTimeout(() => {
            this.fireRefreshEventOnWindow();
        }, 300);
    }

    fireRefreshEventOnWindow = function () {
        const evt = document.createEvent("HTMLEvents");
        evt.initEvent("resize", true, false);
        window.dispatchEvent(evt);
    };

    CloseSidebar() {
        this.layoutService.toggleSidebarSmallScreen(false);
    }

    isTouchDevice() {
        const isMobile = this.deviceService.isMobile();
        const isTablet = this.deviceService.isTablet();

        if (isMobile || isTablet) {
            this.perfectScrollbarEnable = false;
        } else {
            this.perfectScrollbarEnable = true;
        }
    }

    ngOnDestroy() {
        if (this.layoutSub) {
            this.layoutSub.unsubscribe();
        }
        if (this.configSub) {
            this.configSub.unsubscribe();
        }
    }

    protected readonly getSvgPath = getSvgPath;

}
