import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { GendersVM } from "app/admin/models/admin/reservation";
import { ICompletedCourses } from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AppAssets } from "app/shared/data/app-assets";
import { AppConstants } from "app/shared/data/app_constants";
import { InputAppearance } from "app/shared/enums/mat-theme.enum";
import {
  FileModel,
  IUserRequest,
  UserModel,
} from "app/shared/models/general/general";
import { GeneralService } from "app/shared/services/General/general.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-user-data",
  templateUrl: "./user-data.component.html",
  styleUrls: ["./user-data.component.scss"],
})
export class UserDataComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;

  passwordForm: FormGroup;
  submittedPasswords: boolean = false;

  isViewMode: boolean = true;
  userData: UserModel;
  completedCourses: ICompletedCourses[];
  genderList: GendersVM[];
  picturePath?: any;

  appearance: InputAppearance = InputAppearance.standard;

  constructor(
    private fb: FormBuilder,
    private toastr: MessagesService,
    private GeneralServ: GeneralService,
    private fileService: UploadFileService,
    private AppConstants: AppConstants,
    private spinner: NgxSpinnerService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private courseService: CourseService
  ) {}

  ngOnInit(): void {
    this.returnProfile();

    this.myForm = this.fb.group({
      first_name: [
        "",
        [
          Validators.required,
          Validators.pattern(
            /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
          ),
          CustomeValidator.whiteSpace,
        ],
      ],
      last_name: [
        "",
        [
          Validators.required,
          Validators.pattern(
            /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
          ),
          CustomeValidator.whiteSpace,
        ],
      ],
      mobile: [
        "",
        [
          Validators.required,
          Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
          Validators.minLength(this.AppConstants.MobileLength),
          Validators.maxLength(this.AppConstants.MobileLength),
          CustomeValidator.MobileStartsWith,
        ],
      ],
      account_email: ["", [Validators.required, Validators.email]],
      address: [null],
      date_of_birth: [null, [Validators.required]],
      gender_id: [null, [Validators.required]],
      profilePicture: [""],
    });

    this.passwordForm = this.fb.group(
      {
        account_password: [
          "",
          [
            Validators.required,
            CustomeValidator.whiteSpace,
            Validators.minLength(3),
          ],
        ],
        newPassword: [
          null,
          [
            Validators.required,
            CustomeValidator.whiteSpace,
            Validators.minLength(3),
          ],
        ],
        ConfirmPassword: [null],
      },
      {
        validator: CustomeValidator.mustMatch("newPassword", "ConfirmPassword"),
      }
    );

    this.getAllGenders();
    this.getCompletedCourses();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  get passwordFormCtrls() {
    return this.passwordForm.controls;
  }

  returnProfile(isUpdate: boolean = false) {
    this.spinner.show();
    this.GeneralServ.returnProfile().subscribe((res: UserModel) => {
      this.userData = res;
      this.picturePath = this.fileService.previewPicture(res.picturePath);
      this.userDataToEdit();
      if (isUpdate) {
        this.GeneralServ.userData.next(res);
        this.isViewMode = true;
      }
      this.spinner.hide();
    });
  }

  userDataToEdit() {
    this.myForm.patchValue(this.userData.user);
    this.FormCtrls.profilePicture.setValue(this.userData.picturePath);
    this.passwordFormCtrls.account_password.reset();
    this.passwordFormCtrls.newPassword.reset();
    this.passwordFormCtrls.ConfirmPassword.reset();
  }

  saveUserData() {
    this.submitted = true;
    if (this.myForm.valid) {
      if (!this.passwordFormCtrls.account_password.valid) {
        this.toastr.warning("enterPassword");
        return;
      }

      let model: IUserRequest = {
        first_name: this.FormCtrls.first_name.value,
        last_name: this.FormCtrls.last_name.value,
        mobile: this.FormCtrls.mobile.value,
        address: this.FormCtrls.address.value,
        account_email: this.FormCtrls.account_email.value,
        date_of_birth: this.FormCtrls.date_of_birth.value,
        gender_id: this.FormCtrls.gender_id.value,
        profilePicture: this.FormCtrls.profilePicture.value,
        account_password: this.passwordFormCtrls.account_password.value,
      };

      this.editProfile(model);
    }
  }

  changePassword() {
    this.submittedPasswords = true;
    if (this.passwordForm.valid) {
      let model: IUserRequest = {
        first_name: this.userData.user.first_name,
        last_name: this.userData.user.last_name,
        mobile: this.userData.user.mobile,
        address: this.userData.user.address,
        account_email: this.userData.user.account_email,
        date_of_birth: this.userData.user.date_of_birth,
        gender_id: this.userData.user.gender_id,
        profilePicture: this.userData.picturePath,
        account_password: this.passwordFormCtrls.account_password.value,
        newPassword: this.passwordFormCtrls.newPassword.value,
        ConfirmPassword: this.passwordFormCtrls.ConfirmPassword.value,
      };

      this.editProfile(model);
    }
  }

  editProfile(model: IUserRequest) {
    this.spinner.show();
    this.GeneralServ.editProfile(model).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.toastr.success("General.UpdateSuccessfully");
        this.returnProfile(true);
        this.submittedPasswords = false;
        this.submitted = false;
      } else if (res.returnValue == 22) {
        this.toastr.error("labels.EnterCorrectPassword");
      } else this.toastr.error(res.returnString);
      this.spinner.hide();
    });
  }

  onChangeImage(file: FileModel) {
    this.picturePath = file.FileAsBase64;
  }

  getCourseCategories(course: any): string {
    let category: String[] = [];
    course.course_category.forEach((ele, index) => {
      // if (index != 0)
      category.push(ele.name);
    });
    return category.join(", ");
  }

  getEmployeeJobs(): string {
    let job: String[] = [];
    this.userData?.userJobTitle?.forEach((ele, index) => {
      // if (index != 0)
      job.push(
        this.languageService.isArabic
          ? ele.ar_name ?? ele.en_name
          : ele.en_name ?? ele.ar_name
      );
    });
    return job.join(", ");
  }

  getAllGenders() {
    this.GeneralServ.getAllGenders().subscribe((res: GendersVM[]) => {
      this.genderList = res;
    });
  }

  getCompletedCourses() {
    this.spinner.show();
    this.courseService
      .getCompletedCourses()
      .subscribe((res: ICompletedCourses[]) => {
        this.completedCourses = res;
        this.spinner.hide();
      });
  }
}
