import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { giftGalleryViewModel } from "app/admin/models/admin/gifts";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-buy-gift-dialog",
  templateUrl: "./buy-gift-dialog.component.html",
  styleUrls: ["./buy-gift-dialog.component.scss"],
})
export class BuyGiftDialogComponent implements OnInit {
  gift: giftGalleryViewModel;
  isDone: boolean = false;
  hasUpdate: boolean = false;

  constructor(
    public languageService: LanguageService,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    private giftsService: GiftsService,
    public dialogRef: MatDialogRef<BuyGiftDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { gift: giftGalleryViewModel }
  ) {
    this.gift = data.gift;
  }

  ngOnInit(): void {
    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.hasUpdate);
  }

  Buy() {
    this.spinner.show();
    this.giftsService.buyGift(this.gift.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.toastr.success("Gifts.giftBoughtSuccessfully");
        this.isDone = true;
        this.hasUpdate = true;
      } else {
        this.toastr.error("Gifts.somethingWentWrong");
      }
      this.spinner.hide();
    });
  }
}
