import {Component, EventEmitter, Input, Output} from "@angular/core";
import {Router} from "@angular/router";

@Component({
  selector: "create-new-btn",
  templateUrl: "./create-new-btn.component.html",
  styleUrls: ["./create-new-btn.component.scss"],
})
export class CreateNewBtnComponent {
  @Input() url: string;
  @Input() label?: string;
  @Input() params: any;
  @Input() class?: any;
  @Input() hideDefaultTitle?: boolean = false;
  @Output() onClick? = new EventEmitter<any>();

  constructor(private router: Router) {
  }

  click($event) {
    // this.router.navigate([this.url], {queryParams: this.params});
    this.router.navigateByUrl("/", {skipLocationChange: true}).then(() => {
      this.router.navigate([this.url], {queryParams: this.params});
    });
    this.onClick.emit($event);
  }
}
