import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ICourseDetailsModel } from "app/admin/models/admin/setup/course";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class UserCourseService extends AppClientService {
  courseData$ = new BehaviorSubject<ICourseDetailsModel>(null);

  constructor(private http: HttpClient) {
    super(http);
  }
}
