import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { FileModel } from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-button-file-input",
  templateUrl: "./button-file-input.component.html",
  styleUrls: ["./button-file-input.component.scss"],
})
export class ButtonFileInputComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() accept?: string;
  @Input() removeFile?: boolean;
  @Input() htmlId: string;
  @Input() showIcon: boolean = true;
  @Input() icon: string = "fa fa-upload";
  @Input() isIconAfter: boolean = false;
  @Input() btnClass: string = "btn btn-info bg-light-info btn-sm";
  @Input() label: string = "General.Upload";
  @Output() onChange = new EventEmitter<FileModel>();

  constructor(
    private fileService: UploadFileService,
    private msg: MessagesService
  ) {}

  ngOnInit(): void {}
  ngOnChanges(): void {
    if (this.removeFile) {
      this.fileService.resetFileById(this.htmlId);
    }
  }

  async uploadFile(event) {
    await this.fileService.uploadFile(event).then((file: FileModel) => {
      // file to large > 10 .MB
      if (file.size > 10485760) {
        this.msg.warning("General.largFileError");
        if (this.htmlId) this.fileService.resetFileById(this.htmlId);
      } else {
        setTimeout(() => {
          this.control?.setValue(file);
          this.onChange.emit(file);
        }, 100);
      }
    });
  }
}
