import { AuthService } from "app/shared/services/auth/auth.service";
import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormControl } from "@angular/forms";
import { LanguageService } from "app/shared/services/language.service";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { IDiplomaGetVM } from "app/admin/models/admin/setup/course";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "diploma-dropdown",
  templateUrl: "./diploma-dropdown.component.html",
  styleUrls: ["./diploma-dropdown.component.scss"],
})
export class DiplomaDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  @Input() reload: boolean = false;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  EduCompId: number;
  diplomaList: IDiplomaGetVM[];

  constructor(
    private courseService: CourseService,
    public languageService: LanguageService,
    private authService: AuthService
  ) {
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllDiploma();
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.control.setValue(null);
      this.getAllDiploma();
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.reload) this.getAllDiploma();
  }

  getAllDiploma() {
    this.courseService
      .getAllDiplomasDropDown<IDiplomaGetVM[]>(this.EduCompId)
      .subscribe((response) => {
        if (response) {
          this.diplomaList = response;
          this.reload = false;
        }
      });
  }
}
