import {RouteInfo} from "../../vertical-menu/vertical-menu.metadata";
import {HttpClient} from "@angular/common/http";
import {Injectable, inject} from "@angular/core";
import {BehaviorSubject} from "rxjs";
import {map} from "rxjs/operators";
import {AppClientService} from "../app-client.service";
import {BreadcrumbService} from "xng-breadcrumb";

@Injectable({
    providedIn: "root",
})
export class UserFunctionsService extends AppClientService {
    menuItems = new BehaviorSubject<any>([]);
    private breadcrumbService = inject(BreadcrumbService);

    constructor(private http: HttpClient) {
        super(http);
    }

    getUserFunctions(branchId: number) {
        return this.get(`usermanagement/return-functions/${branchId}`).subscribe(
            (res: any) => {
                res.Groups.map((menuItems) => {
                    menuItems.class = "dropdown nav-item has-sub";
                    menuItems.icon = "ft-align-left";
                    menuItems.isExternalLink = false;
                    menuItems.badge = "";
                    menuItems.badgeClass = "";
                    menuItems.submenu.map((item) => {
                        item.class = "dropdown-item";
                        item.icon = "ft-arrow-right submenu-icon";
                        item.isExternalLink = false;
                        item.badge = "";
                        item.badgeClass = "";
                        this.breadcrumbService.set(item.path, {
                            label: item.title,
                            disable: true,
                        });
                        // this.breadcrumbService.breadcrumbs$.subscribe((res) => {
                        //   console.log(res);
                        // });
                    });
                });

                res.GrouplessFunctions.sort((a, b) => a.priorityNumber - b.priorityNumber);
                res.GrouplessFunctions.map((menuItems) => {
                    menuItems.class = "dropdown nav-item";
                    menuItems.icon = menuItems.cssClass ?? "ft-align-left";
                    menuItems.isExternalLink = false;
                    menuItems.submenu = [];
                    this.breadcrumbService.set(menuItems.path, {
                        label: menuItems.title,
                        disable: true,
                    });
                });

                this.menuItems.next(res.Groups.concat(res.GrouplessFunctions));
            }
        );
    }
}
