import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ICourses } from "app/admin/models/admin/course-request";
import {
  ICompletedCourses,
  ICourseDetailsModel,
  ICourseObjectiveAddEditVM,
  ICourseObjectiveGetModel,
  IDiplomaDetailsModel,
  ILastViewedCourseModel,
  IMyCoursesMV,
  ISearchCourse,
  ISearchDiploma,
  ISimilarCourse,
  ISolvedExamsModel,
  IUnitAttachmentAddEditVM,
  IUnitAttachmentResponseVM,
  IUnitResponseVM,
} from "app/admin/models/admin/setup/course";
import { IUnitAttachmentGetVM } from "app/admin/models/admin/setup/course";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CourseService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  ////// main course > diploma
  getAllDiplomasBySearch<T>(data: ISearchDiploma) {
    return this.post<T>(`Course/SearchDiploma`, data);
  }

  getAllDiplomasHome(userId: number) {
    return this.get(`Diploma/getAllDiploma/${userId}`);
  }

  // getAllDiplomas<T>(page: number) {
  //   return this.get<T>(
  //     `Course/geAllCourses/${page}`
  //   );
  // }

  getDiplomaById<T>(id: number) {
    return this.get<T>(`Course/getCourseById/${id}`);
  }

  addDiploma<T, TV>(course: T) {
    return this.post<TV>(`Course/PostCourse`, course);
  }

  updateDiploma<T, TV>(id: number, course: T) {
    return this.put<TV>(`Course/PutCourses/${id}`, course);
  }

  removeDiploma(id: number) {
    return this.delete(`Course/DeleteCourses/${id}`);
  }

  getAllDiplomasDropDown<T>(eduCompId: number) {
    return this.get<T>(`Course/geCourses/${eduCompId}`);
  }

  getDiplomaDetails(id: number) {
    return this.get<IDiplomaDetailsModel>(`Diploma/getDiplomaById/${id}`);
  }

  ////// sub course > course
  getAllCoursesBySearch<T>(data: ISearchCourse) {
    return this.post<T>(`SubCourse/searchSubCourse`, data);
  }

  // getAllCourses<T>(page: number) {
  //   return this.get<T>(
  //     `SubCourse/getAllSubCourse/${page}`
  //   );
  // }

  getCoursesByJobTitleDropdown(depJobTitleId: number) {
    return this.get<ICourses>(
      `Subject/getSubjectsByDepartmentJobtitleId/${depJobTitleId}`
    );
  }

  addCourse<T, TV>(course: T) {
    return this.post<TV>(`SubCourse/PostSubCourse`, course);
  }

  getCourseById<T>(id: number) {
    return this.get<T>(`SubCourse/getSubCourseById/${id}`);
  }

  updateCourse<T, TV>(id: number, course: T) {
    return this.put<TV>(`SubCourse/PutSubCourse/${id}`, course);
  }

  removeCourse(id: number) {
    return this.delete(`SubCourse/DeleteSubCourses/${id}`);
  }

  updateJobTitleCourse<T, TV>(id: number, dates: T) {
    return this.put<TV>(`SubCourse/EditSubCourseDepJob/${id}`, dates);
  }

  removeJobTitleCourse(id: number) {
    return this.delete(`SubCourse/DeleteJobTitleOfSubCourse/${id}`);
  }

  getCourseDetails(id: number) {
    return this.get<ICourseDetailsModel>(`Diploma/getCourseDetails/${id}`);
  }

  getCompletedCourses() {
    return this.get<ICompletedCourses[]>(`Course/GetAllCompletedCourse`);
  }

  // Start add users to course
  getAllUsers<T>(page: number, subCourseID: number) {
    return this.get<T>(`UserSubject/geAllUserBySubId/${page}/${subCourseID}`);
  }

  checkUser<T, TV>(body: T, eduCompId: number) {
    // console.log(body, eduCompId)
    return this.post<TV>(
      `UserSubject/getUserSubjectByEduCompId/${eduCompId}`,
      body
    );
  }

  addUser<T, TV>(obj: T) {
    return this.post<TV>(`UserSubject/addUserSubject`, obj);
  }

  deleteUser(subUserID: number) {
    return this.delete(`UserSubject/DeleteUserSubject/${subUserID}`);
  }

  uploadUsersByExcel<T>(obj: T) {
    return this.post(`UserSubject/addMultiUserSubject`, obj);
  }

  getMyCourses<T>() {
    return this.get<T>("UserSubject/getAllUserCourses");
  }

  getBasicAndRecommendedCourses() {
    return this.get("UserSubject/getBasicRecommendedCourses");
  }

  getLastCourseUser() {
    return this.get<ILastViewedCourseModel>(`Admin/GetLastUserCourse`);
  }

  getSimilarCourses(courseId: number) {
    return this.get<ISimilarCourse[]>(
      `Course/GetCoursesSimilarToCompleteCourse/${courseId}`
    );
  }

  //#region ///////////////// course structure
  getAllUnitData(unitId: number) {
    return this.get<IUnitResponseVM>(`Admin/GetAllUnitDate/${unitId}`);
  }

  getUnitAttachmentById(attachId: number) {
    return this.get<{ fileEduUnit: IUnitAttachmentGetVM[] }>(
      `FileEducationalUnitController/GetFileEduUnitById/${attachId}`
    );
  }

  addEditUnitAttachment(data: IUnitAttachmentAddEditVM) {
    return this.post(`FileEducationalUnitController/AddEditFileEduUnit`, data);
  }

  deleteUnitAttachment(attachId: number) {
    return this.delete(
      `FileEducationalUnitController/deleteFileEduUnit/${attachId}`
    );
  }

  getAllUnitAttachments(courseId: number) {
    return this.get<{ UnitData: IUnitAttachmentResponseVM[] }>(
      `Admin/GetAllUnitFile/${courseId}`
    );
  }

  getAllUnitGrades(unitId: number) {
    return this.get<ISolvedExamsModel[]>(
      `Admin/GetAllSolvedUnitExams/${unitId}`
    );
  }
  //#endregion

  //#region ///////////////// course objectives
  getAllCourseObjectives(courseId: number, page: number) {
    return this.get<ICourseObjectiveGetModel>(
      `CourseObjective/getAllCourseObjectiveById/${courseId}/${page}`
    );
  }

  addEditCourseObjective(data: ICourseObjectiveAddEditVM) {
    return this.post(`CourseObjective/AddEditCourseObjective`, data);
  }

  deleteCourseObjective(objective: number) {
    return this.delete(`CourseObjective/DeleteCourseObjective/${objective}`);
  }
  //#endregion
}
