import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class JopTitleDepartmentService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getJopTitlebyDepartment<T>(userid: number, page: number) {
    return this.get<T>(
      `usermanagement/returnAllUserDepartments/${userid}/${page}`
    );
  }

  addJopTitlebyDepartment<T>(model: T) {
    return this.post<T>(`usermanagement/CreateUserDepartmentJobTitle`, model);
  }

  editAllDeparment<T>(model: T, id: number) {
    return this.put<T>(
      `usermanagement/EditUserDepartmentJobTitle/${id}`,
      model
    );
  }

  editJobTitle<T>(model: T, id: number) {
    // console.log("model", model);
    // console.log("j", id);
    return this.put<T>(`usermanagement/editJobTitleforuser/${id}`, model);
  }

  deleteUserDeptJobTitle(userid: number, id: number) {
    return this.delete(`usermanagement/deleteUserDepartment/${userid}/${id}`);
  }

  deleteJopTitle(id: number) {
    // console.log(`usermanagement/deleteJobTitleForuser/${id}`);
    return this.delete(`usermanagement/deleteJobTitleForuser/${id}`);
  }
}
