import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IFavoriteRequest, IFavoriteResponse } from "../models/favorite";

@Injectable({
  providedIn: "root",
})
export class FavoriteCourseDiplomaService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  GetAll() {
    return this.get<IFavoriteResponse>("FavoriteCourse/GetFavorite");
  }

  Create(model: IFavoriteRequest) {
    return this.post("FavoriteCourse/AddFavorite", model);
  }

  Delete(id: number) {
    return this.delete(`FavoriteCourse/DeleteFavorite/${id}`);
  }
}
