export class SvgManager {
    public readonly nav_categories: string = "assets/svg/nav-icons/categories.svg";
    public readonly nav_courses: string = "assets/svg/nav-icons/courses.svg";
    public readonly nav_dashboard: string = "/assets/svg/nav-icons/dashboard.svg";
    public readonly nav_employees: string = "/assets/svg/nav-icons/employees.svg";
    public readonly nav_gift_gallery: string = "/assets/svg/nav-icons/gift-gallery.svg";
    public readonly nav_learning_requests: string = "/assets/svg/nav-icons/learning-requests.svg";
    public readonly nav_teaching_requests: string = "/assets/svg/nav-icons/teaching_requests.svg";

}

export function getSvgPath(val) {
    const svgManager = new SvgManager();
    return svgManager[val] ?? null;
}
