import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { LanguageService } from "app/shared/services/language.service";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "courses-by-dep-job-title",
  templateUrl: "./courses-by-dep-job-title.component.html",
  styleUrls: ["./courses-by-dep-job-title.component.scss"],
})
export class CoursesByDepJobTitleComponent implements OnInit {
  @Input() control: FormControl;
  @Input() jobTitleControl: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  coursesList: any[];

  constructor(
    private courseService: CourseService,
    public languageService: LanguageService
  ) {}

  ngOnInit(): void {
    this.control?.disable();

    this.jobTitleControl.valueChanges.subscribe((jobTitleId) => {
      this.control.setValue(null);
      if (jobTitleId > 0) {
        this.getAllJobTitles(jobTitleId);
        this.control.enable();
      } else {
        this.control.disable();
      }
    });
  }

  getAllJobTitles(jobTitleId: number) {
    this.courseService
      .getCoursesByJobTitleDropdown(jobTitleId)
      .subscribe((res) => {
        if (res) this.coursesList = res.Courses;
      });
  }
}
