import {
  Component,
  Input,
  OnInit,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import {FormControl} from "@angular/forms";
import {MatSelect} from "@angular/material/select";
import {LanguageService} from "app/shared/services/language.service";
import {ReplaySubject, Subject} from "rxjs";
import {take, takeUntil} from "rxjs/operators";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

interface GeneralVM {
  id: number;
  ar_name: String;
  en_name: string;
}

@Component({
  selector: "dropdown-with-search",
  templateUrl: "./dropdown-with-search.component.html",
  styleUrls: ["./dropdown-with-search.component.scss"],
})
export class DropdownWithSearchComponent implements OnInit {
  @Input() control: FormControl;
  @Input() filterControl: FormControl;
  @Input() label: string;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  @Input() List: GeneralVM[] = [];
  filteredList: ReplaySubject<GeneralVM[]> = new ReplaySubject<GeneralVM[]>(1);

  @ViewChild("singleSelect") singleSelect: MatSelect;

  // Subject that emits when the component has been destroyed
  private _onDestroy = new Subject<void>();

  constructor(public languageService: LanguageService) {
  }

  ngOnInit(): void {
    this.filteredList.next(this.List.slice());

    // listen for search field value changes
    this.filterControl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterCategories();
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.List.currentValue != changes.List.previousValue) {
      this.filteredList.next(this.List.slice());
    }
  }

  ngAfterViewInit() {
    this.setInitialValue();
  }

  // Sets the initial value after the filteredList are loaded initially
  private setInitialValue() {
    this.filteredList
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(() => {
        // setting the compareWith property to a comparison function
        // triggers initializing the selection according to the initial value of
        // the form control (i.e. _initializeSelection())
        // this needs to be done after the filteredList are loaded initially
        // and after the mat-option elements are available
        this.singleSelect.compareWith = (a: GeneralVM, b: GeneralVM) => a === b;
      });
  }

  private filterCategories() {
    if (!this.List) {
      return;
    }
    // get the search keyword
    let search = this.filterControl.value;
    if (!search) {
      this.filteredList.next(this.List.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the list
    this.filteredList.next(
      this.List.filter(
        (category) =>
          category.ar_name.toLowerCase().indexOf(search) > -1 ||
          category.en_name.toLowerCase().indexOf(search) > -1
      )
    );
  }

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
  }
}
