import {IsSupportGuard} from "./../services/auth/Guards/roles/is-support.guard";
import {Routes} from "@angular/router";
import {IsStudentGuard} from "../services/auth/Guards/roles/is-student.guard";
import {UserDataComponent} from "../components/user-data/user-data.component";
import {IsUserGuard} from "../services/auth/Guards/auth/is-user.guard";
import {AccessAllowGuard} from "../services/auth/Guards/roles/access-allow.guard";
import {DashboardComponent} from "app/admin/pages/dashboard/dashboard.component";

//Route for content layout with sidebar, navbar and footer.

export const Full_ROUTES: Routes = [
  {
    path: "home",
    loadChildren: () =>
      import("../../home/home.module").then((m) => m.HomeModule),
    data: {
      breadcrumb: {
        label: "Navbar.Home",
        info: "ft-home",
      },
    },
  },

  {
    path: "profile",
    component: UserDataComponent,
    canActivate: [IsUserGuard],
  },

  // Admin
  {
    path: "dashboard",
    //component: DashboardComponent,
    // canActivate: [AccessAllowGuard],
    loadChildren: () =>
      import("../../admin/dashboard.module").then((m) => m.DashboardModule),
    data: {
      breadcrumb: {
        label: "Navbar.Dashboard",
        view: "dashboard",
        disable: true,
      },
    },
  },

  // Assistant
  {
    path: "dashboard",
    // canActivate: [AccessAllowGuard],
    loadChildren: () =>
      import("../../assistant/assistant.module").then((m) => m.AssistantModule),
    data: {
      breadcrumb: {
        label: "Navbar.Dashboard",
        disable: true,
      },
    },
  },

  // Student
  {
    path: "student",
    loadChildren: () =>
      import("../../student/student.module").then((m) => m.StudentModule),
    canActivate: [IsStudentGuard],
    data: {
      breadcrumb: {
        label: "labels.Student",
        disable: true,
      },
    },
  },

  // Student
  {
    path: "support",
    loadChildren: () =>
      import("../../support/support.module").then((m) => m.SupportModule),
    canActivate: [IsSupportGuard],
  },
];
