import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Injectable} from "@angular/core";
import {Router} from "@angular/router";
import {AppStrings} from "app/shared/data/app-strings";
import {BehaviorSubject, Subject} from "rxjs";
import {AppClientService} from "app/shared/services/app-client.service";
import {Storage} from "app/shared/services/local-storage";
import {UserTypes} from "app/shared/enums/userTypes.enum";
import {ConfigService} from "app/shared/services/config.service";

@Injectable({
  providedIn: "root",
})
export class AuthService extends AppClientService {
  EduCompId = new Subject<any>();
  branchId = new Subject<any>();
  userFunctions = new BehaviorSubject<number[]>([]);
  isUser$ = new BehaviorSubject<boolean>(this.isUserLoggedIn());


  headers = new HttpHeaders().append("Content-Type", "application/json");

  constructor(
    private http: HttpClient,
    private router: Router,
    private appStrings: AppStrings,
  ) {
    super(http);
  }

  //#region Registeration

  // START Registeration
  Registeration<T, TE>(data: T) {
    return this.post<TE>("APIUsers/Register", data);
  }

  // START LOGIN
  login<T, TE>(loginData: T) {
    return this.post<TE>("user/login", loginData);
  }

  //START LOGOUT
  logout() {
    Storage.customClearStorag(this.appStrings.CURRENT_LANG);
    this.isUser$.next(false)
    this.router.navigateByUrl("/home");
  }

  // START Forgot Password
  forgotPassword(email: any) {
    return this.post("APIUsers/forgotPassword", email);
  }

  // START Reset Password
  resetPassword(data: any) {
    return this.post("APIUsers/resetPasswordByVerificationCode", data);
  }

  getUserFunctions(branchId: any) {
    return this.get(`usermanagement/return-functions/${branchId}`);
  }

  getUserRowFunctions(parentId: any) {
    return this.get(
      `usermanagement/return-children-functions-by-parentId/${parentId}`
    );
  }

  //#endregion

  //#region  Chick Users
  //================= Start  Chick Users ==================
  isUserLoggedIn() {
    return Storage.isUserLoggedIn();
  }

  // isUserLoggedIn() {
  //   return Storage.getToken() ?? false;
  // }

  isTeacher() {
    if (Storage.getUserTypeId() == UserTypes.teacher && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isStudent() {
    if (Storage.getUserTypeId() == UserTypes.student && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isParent() {
    if (Storage.getUserTypeId() == UserTypes.parent && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isAdmin() {
    if (Storage.getUserTypeId() == UserTypes.admin && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isCenter() {
    if (Storage.getUserTypeId() == UserTypes.center && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isSupport() {
    if (Storage.getUserTypeId() == UserTypes.support && this.isUserLoggedIn()) {
      return true;
    } else return false;
  }

  isFinancial() {
    if (
      Storage.getUserTypeId() == UserTypes.financial &&
      this.isUserLoggedIn()
    ) {
      return true;
    } else return false;
  }

  isAssistant() {
    if (
      Storage.getUserTypeId() == UserTypes.assistant &&
      this.isUserLoggedIn()
    ) {
      return true;
    } else return false;
  }

  //#endregion

  // isUser GetToken
  UserFunctions() {
    return this.userFunctions;
  }

  setUserFunctions(userFunction: number[]) {
    this.userFunctions.next(userFunction);
  }

  getEduCompId() {
    return Storage.getEduCompId();
  }

  setEduCompId(EduCompId) {
    Storage.setEduCompId(EduCompId);
    this.EduCompId.next({EduCompId});
  }

  getBranchId() {
    return Storage.getBranchId();
  }

  setBranchId(branchId) {
    Storage.setBranchId(branchId);
    this.branchId.next({branchId});
  }

  //Current YearId
  CurrentYearId() {
    return Storage.getYearId();
  }

  // check Authorization using view
  chechAuthorization(view: string) {
    return this.get(`usermanagement/returnUserView/${view}`);
  }
} //end class
