import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { ThemePalette } from "@angular/material/core";
import { MatColors } from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "slide-toggle",
  templateUrl: "./slide-toggle.component.html",
  styleUrls: ["./slide-toggle.component.scss"],
})
export class SlideToggleComponent implements OnChanges {
  @Input() control: FormControl;
  // @Input() submitted?: boolean = false;
  @Input() label: string;
  @Input() labelPosition?: string = "after"; // 'before' | 'after'
  @Input() disabled?: boolean = false;
  @Input() submitted?: boolean = false;
  @Input() class?: string;
  @Input() id?: string;
  @Input() color?: ThemePalette = MatColors.primary;
  @Output() onChange? = new EventEmitter<boolean>();

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.disabled?.currentValue) this.control?.disable();
    else if (!changes.disabled?.currentValue) this.control?.enable();
  }

  change(value: boolean) {
    this.onChange.emit(value);
  }
}
