import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { LanguageService } from "app/shared/services/language.service";
import { IJobTitleByDeptVM } from "app/admin/models/setup/job-title";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

@Component({
  selector: "job-title-by-department-dropdown",
  templateUrl: "./job-title-by-department-dropdown.component.html",
  styleUrls: ["./job-title-by-department-dropdown.component.scss"],
})
export class JobTitleByDepartmentDropdownComponent implements OnInit {
  @Input() control: FormControl;
  @Input() departmentControl: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  // use dep_job_id OR only job_id
  @Input() depJobId: boolean = true;
  @Input() appearance: InputAppearance = defaultInputAppearance;
  jobTitleList: IJobTitleByDeptVM[];

  constructor(
    private jobTitleService: JobTitleService,
    public languageService: LanguageService
  ) {}

  ngOnInit(): void {
    this.control?.disable();

    this.departmentControl.valueChanges.subscribe((departmentId) => {
      this.control.setValue(null);
      if (departmentId > 0) {
        this.getAllJobTitles(departmentId);
        this.control.enable();
      } else {
        this.control.disable();
      }
    });
  }

  getAllJobTitles(departmentId: number) {
    this.jobTitleService
      .getJobTitleByDepartment<IJobTitleByDeptVM[]>(departmentId)
      .subscribe((res) => {
        if (res) this.jobTitleList = res;
      });
  }
}
