import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class StudentService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  //#region  Student Profile

  //Return Student Subjects
  returnStudentSubjects<T>() {
    return this.get<T>("AJAX/subjectsIndex");
  }

  //Return Student Subject Evaluation
  returnSubjectEvaluation<T>(id: any) {
    return this.get<T>("AJAX/subjectEvaluation/" + id);
  }

  getSubjectMaterials<T>(subjectId: number) {
    return this.get<T>(
      "AJAX/returnSubcribedAndUnSubcribedLectures/" + subjectId
    );
  }

  //Return Student General Evaluation
  returnGeneralEvaluation<T>() {
    return this.get<T>("Dashboard/Evaluation");
  }

  //Educational Component Request
  EduCompRequest(data: any) {
    return this.post("AJAX/EduComponentRequest/", data);
  }

  //Refund Lecture
  refundLecture(lecId: any) {
    return this.get("AJAX/refundLecture/" + lecId);
  }

  //balanceCheck
  balanceCheck<T>() {
    return this.get<T>("AJAX/balanceCheck");
  }

  //returnUserPaymentsHistory
  getPaymentsHistory<T>(id: any) {
    return this.get<T>("AJAX/returnUserPaymentsHistory/" + id);
  }

  //returnUserPayments
  getPayments<T>(id: any) {
    return this.get<T>("AJAX/returnUserPayments/" + id);
  }

  //Request Fawry Code
  payAmount(amount: any, teacherId: any, EduCompId: any, methodId: any) {
    return this.post(
      `AJAX/PayAmount/${amount}/${teacherId}/${EduCompId}/${methodId}`,
      null
    );
  }

  //Request Fawry Code For Tamer ElQady
  payAmountCowPay(amount: any, teacherId: any, EduCompId: any) {
    return this.post(
      `AJAX/payAmountCowPay/${amount}/${teacherId}/${EduCompId}`,
      null
    );
  }

  //pay Amount  By Credit
  payAmountCredit(data: any) {
    return this.post("AJAX/PayAmountCC", data);
  }

  //Student  Education Stage Data
  SaveStudenStage<T, TE>(data: T) {
    return this.post<TE>("AJAX/registerStudent", data);
  }

  //return Student Education Stage Data
  getStudenStageData<T>() {
    return this.get<T>("AJAX/getStudentData");
  }

  //Student  Education Stage Data
  TestFile(data: any) {
    return this.post("AJAX/TestFile", data);
  }
  //#endregion

  //#region  Student Exams
  ////////////////////////////////

  //return Student Exams
  returnStudentExam(examId: any) {
    return this.get("Exam/Details/" + examId);
  }

  //return Student Education Stage Data
  returnStudentExamResults(examId: any) {
    return this.post("Exam/Results/" + examId, null);
  }

  //Submit Single Question
  SubmitSingleQuestion(data: any) {
    return this.post(`Exam/SubmitSingleQuestion`, data);
  }

  //Submit Exam
  SubmitExam(data: any) {
    return this.post("Exam/Submit", data);
  }

  //Generate Exam From Template By templateId
  GenerateExamFromTemplate(templateId: any) {
    return this.post("Exam/GenerateExamFromTemplate/" + templateId, null);
  }
  //#endregion

  //#region Student Reservation
  ////////////////////////////////////

  // Return Inputs
  returnInputsByViewEduCompId(EduCompId: any, viewId: any) {
    return this.post(
      `AJAX/returnInputsByViewEduCompId/${EduCompId}/${viewId}`,
      null
    );
  }

  // Return Policies
  EducationalComponentPolicies(viewId: any, code: any) {
    return this.get(`AJAX/EducationalComponentPolicies/${viewId}/${code}`);
  }

  // Reservation
  SubmitReservation(data: any) {
    return this.post("AJAX/Reservation", data);
  }

  // Return Educational Component Branches
  EducationalComponentTimetable(viewId: any, code: any) {
    return this.get(`AJAX/EducationalComponentTimetable/${viewId}/${code}`);
  }

  // Return Avaliable Dates By BranchId
  GetAvaliableDatesByBranchId(
    BranchId: any,
    MustHaveStudents: boolean,
    DontShowMaxed: boolean
  ) {
    return this.get(
      `AJAX/GetAvaliableDatesByBranchId/${BranchId}/${MustHaveStudents}/${DontShowMaxed}`
    );
  }

  // Submit Date to Branche
  SubmitEducationalComponentTimetable(data: any) {
    return this.post("AJAX/EducationalComponentTimetable", data);
  }

  // return Current Student Educational Component Time Table
  returnCurrentStudentEducationalComponentTimeTable(EduCompId: any) {
    return this.get(
      `AJAX/returnCurrentStudentEducationalComponentTimeTable/${EduCompId}`
    );
  }
  //#endregion

  //#region  Top Students
  //GetTopStudents
  GetTopStudents<T>(EduCompId: any, TeacherId: any, SubjectId: any) {
    return this.get<T>(
      `AJAX/GetTopStudents/${EduCompId}/${TeacherId}/${SubjectId}`
    );
  }

  //Get Top Exam Students By Teacher EduCompId
  GetTopExamStudents<T>(EduCompId: any, TeacherId: any) {
    return this.get<T>(
      `AJAX/GetTopExamStudentsByTeacherEduCompId/${EduCompId}/${TeacherId}`
    );
  }
  //#endregion
}
