import { DatePipe } from "@angular/common";
import { Component, OnInit, Inject } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { TranslateService } from "@ngx-translate/core";
import {
  GetJobDepartment,
  JobTitleByDepartment,
} from "app/admin/models/setup/job-title-by-department";
import { JopTitleDepartmentService } from "app/admin/services/setup/job-title-department.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-update-date",
  templateUrl: "./update-date.component.html",
  styleUrls: ["./update-date.component.scss"],
})
export class UpdateDateComponent implements OnInit {
  myForm!: FormGroup;
  submitted: boolean = false;

  constructor(
    public dialogRef: MatDialogRef<UpdateDateComponent>,
    @Inject(MAT_DIALOG_DATA) public data: GetJobDepartment,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private _serjopTitlebyDepartment: JopTitleDepartmentService,
    private translate: TranslateService,
    private datePipe: DatePipe
  ) {}

  ngOnInit(): void {
    console.log(this.data);
    this.myForm = this.fb.group({
      userid: [this.data.user_dept_job_id],
      fromDate: [this.data.from_date, [Validators.required]],
      toDate: [this.data.to_date],
    });
  }

  get departmentCtrl() {
    return this.myForm.controls;
  }

  updateJobTitle() {
    if (
      this.departmentCtrl["toDate"].value &&
      this.departmentCtrl["fromDate"].value >
        this.departmentCtrl["toDate"].value
    ) {
      this.toastr.error("       ");
    } else {
      this.submitted = true;
      const model = {
        from_date: this.datePipe.transform(this.myForm.value.fromDate),
        to_date: this.datePipe.transform(this.myForm.value.toDate),
      };

      if (this.myForm.valid) {
        this._serjopTitlebyDepartment
          .editJobTitle(model, this.data.user_dept_job_id)
          .subscribe((res: any) => {
            if (res.returnValue == 1) {
              // this.alljoblist=res;
              this.submitted = false;
              this.translate
                .get("jobTitlebyDepartment.toasterSaveSucses")
                .subscribe((res) => {
                  if (res) this.toastr.success(res);
                });
              this.myForm.reset();
              this.dialogRef.close(true);
            } else this.toastr.error(res.returnString);
          });
      }
    }
  }
}
