import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class DepartmentService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllDepartments<T>(page: number, EduCompId: number) {
    return this.get<T>(`Department/getDepartments/${EduCompId}/${page}`);
  }

  getDepartmentsDropDown<T>(EduCompId: number) {
    return this.get<T>(`Department/getAllDepartments/${EduCompId}`);
  }

  // addEditDepartment<T, TV>(obj: T) {

  //   return this.post<TV>(`Department/PostDepartments`, obj);
  // }

  getDepartmentById<T>(id: number) {
    return this.get<T>(`Department/${id}`);
  }

  addDepartment<T>(department: T) {
    return this.post<T>(`Department/PostDepartments`, department);
  }

  editDepartment<T>(department: T, id: number) {
    return this.put<T>(`Department/PutOneDepartments/${id}`, department);
  }

  deleteDepartment(departmentId: number) {
    return this.delete(`Department/DeleteOneDepartments/${departmentId}`);
  }
}
