import {
  HttpClient,
  HttpContext,
  HttpHeaders,
  HttpParams,
} from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class AppClientService {
  constructor(private https: HttpClient) {}
  // RT means response type
  // BT means Body type

  baseUrl = environment.baseUrl + "/api/";
  options: optionsModel;

  get<RT>(url: string) {
    return this.https.get<RT>(this.baseUrl + url, this.options);
  }

  post<RT>(url: string, body: any) {
    return this.https.post<RT>(this.baseUrl + url, body, this.options);
  }

  put<RT>(url: string, body: any) {
    return this.https.put<RT>(this.baseUrl + url, body, this.options);
  }

  delete<RT>(url: string) {
    return this.https.delete<RT>(this.baseUrl + url, this.options);
  }
}

export interface optionsModel {
  headers?: HttpHeaders;
  params?: HttpParams;
  context?: HttpContext;
}
