import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IMyDiplomasMV } from "app/admin/models/admin/setup/course";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { IFavoriteRequest } from "app/student/models/favorite";
import { AddCourseRequestDetailsComponent } from "app/student/pages/requests/add-course-request-details/add-course-request-details.component";
import { FavoriteCourseDiplomaService } from "app/student/services/favorite-course-diploma.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-diploma-card",
  templateUrl: "./diploma-card.component.html",
  styleUrls: ["./diploma-card.component.scss"],
})
export class DiplomaCardComponent implements OnInit {
  @Input() diploma: IMyDiplomasMV;
  @Output() changeFavorites = new EventEmitter<IMyDiplomasMV>();

  constructor(
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private favoriteCourseDiplomaService: FavoriteCourseDiplomaService
  ) {}

  ngOnInit() {
    this.diploma.course_Minutes = this.diploma.totalMinutes % 60;
    this.diploma.course_hours =
      (this.diploma.totalMinutes - this.diploma.course_Minutes) / 60;
  }

  getCategoresTooltip() {
    let category: String[] = [];
    this.diploma.course_category.forEach((ele, index) => {
      if (index != 0) category.push(ele.name);
    });
    return category.join(", ");
  }

  openAddRquestDialog(): void {
    this.dialog
      .open(AddCourseRequestDetailsComponent, {
        data: {
          diploma: this.diploma,
        },
        maxWidth: "700px",
        minWidth: "50vw",
        autoFocus: false,
        panelClass: "dialog-radius",
      })
      .afterClosed()
      .subscribe((isUpdated) => {
        if (isUpdated) this.diploma.isPending = true;
      });
  }

  changeCourseFavorite() {
    this.spinner.show();
    // delete favorite
    if (this.diploma.isFavorite) {
      this.favoriteCourseDiplomaService
        .Delete(this.diploma.id)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.diploma.isFavorite = false;
            this.toastr.success("General.DeleteSuccessfully");
            this.changeFavorites.emit(this.diploma);
          } else this.toastr.error(res.returnString);
        });
    }
    // add favorite
    else {
      let model: IFavoriteRequest = {
        course_id: null,
        diploma_id: this.diploma.id,
      };
      this.favoriteCourseDiplomaService.Create(model).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.diploma.isFavorite = true;
          this.toastr.success("General.AddSuccessfully");
          this.changeFavorites.emit(this.diploma);
        } else this.toastr.error(res.returnString);
      });
    }
    this.spinner.hide();
  }
}
