import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GendersVM } from "app/admin/models/admin/reservation";
import { IUserRequest, UserModel } from "app/shared/models/general/general";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class GeneralService extends AppClientService {
  userData = new BehaviorSubject<UserModel>(null);
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllEduCompList() {
    return this.get("EduComponent");
  }

  returnProfile() {
    return this.get("AJAX/returnProfile");
  }

  editProfile(data: IUserRequest) {
    return this.post("AJAX/editProfile", data);
  }

  getAllEduStagesList<T>() {
    return this.get<T>("AJAX/getStages");
  }

  getEduYearByStageId<T>(stageId: any) {
    return this.get<T>("AJAX/getEducationalYear/" + stageId);
  }

  getSubjectsByYearId<T>(yearId: any) {
    return this.get<T>("AJAX/getSubjects/" + yearId);
  }

  getTeachersBySubjectId<T>(subjectId: any) {
    return this.get<T>("AJAX/getTeachers/" + subjectId);
  }

  getTeachersMaterials<T>(subjectId: number, teacherId: number) {
    return this.get<T>(
      `AJAX/returnMaterialsByMainSubjectTeacherId/${subjectId}/${teacherId}`
    );
  }

  getUserBoughtedMaterials<T, TE>(data: TE) {
    return this.post<T>("AJAX/getUserMaterialsByTeacherSubject", data);
  }

  getEduCompByUser<T>() {
    return this.get<T>("AJAX/returnEducationalComponentByUser");
  }

  getBranchesByEduComp<T>(EduCompId: number) {
    return this.get<T>(`usermanagement/returBranchesByEduCompId/${EduCompId}`);
  }

  returnStages(teacherId: any) {
    return this.get("AJAX/returnStages/" + teacherId);
  }

  getAllCountriesList<T>() {
    return this.get<T>("Admin/getAllCountriesList");
  }

  getTeachersByEducompId(EduCompId: any) {
    return this.get("Admin/get-all-teachers/" + EduCompId);
  }

  getTeacherBySubjectAndEduCompId(EduCompId: any, subjectId: any) {
    return this.get(`Admin/getTeachersByEduCompID/${EduCompId}/${subjectId}`);
  }

  getSubjectByTeacherId(teacherId: number, yearId?: number) {
    return this.get(`Admin/get-all-lectures/${teacherId}/${yearId}`);
  }

  getAllNotifications(yearId: any, page: any) {
    return this.get("AJAX/returnNotificationsByUserId/" + yearId + "/" + page);
  }

  ReadNotification(id: any) {
    return this.get("AJAX/ReadNotification/" + id);
  }

  getAllGenders() {
    return this.get<GendersVM[]>("AJAX/getGender");
  }
}
