import { AppStrings } from "app/shared/data/app-strings";

export class Storage {
  static appStrings: AppStrings = new AppStrings();

  static setItem(key: string, value: any) {
    localStorage.setItem(key, value);
  }

  static getItem(key: string) {
    return localStorage.getItem(key);
  }

  static removeItem(key: string) {
    localStorage.removeItem(key);
  }

  static customClearStorag(keyToException: string) {
    Object.keys(localStorage).forEach((key) => {
      if (!key.includes(keyToException)) delete localStorage[key];
    });
  }

  // ==========================================================================================================================
  // Start Set & get [Language]
  static setLocalLang(lang: string) {
    Storage.setItem(this.appStrings.CURRENT_LANG, lang);
  }

  static getLocalLang() {
    return Storage.getItem(this.appStrings.CURRENT_LANG);
  }

  // Start set & get [user data]

  // Start set & get [TOKEN] ==============================================
  static setToken(token: string) {
    Storage.setItem(this.appStrings.TOKEN, token);
  }

  static getToken() {
    return Storage.getItem(this.appStrings.TOKEN ?? null);
  }

  // Start set & get [user type id] ===========================================
  static setUserTypeId(user_type_id: any) {
    Storage.setItem(this.appStrings.USER_TYPE_ID, user_type_id);
  }

  static getUserTypeId(): number {
    return Number(Storage.getItem(this.appStrings.USER_TYPE_ID));
  }

  // Start set & get [year id] ===========================================
  static setYearId(YearId: any) {
    Storage.setItem(this.appStrings.YEAR_ID, YearId);
  }

  static getYearId(): number {
    return Number(Storage.getItem(this.appStrings.YEAR_ID));
  }

  // Start set & get [eduCompList] ===========================================
  static setEduCompList(eduCompList: number[]) {
    Storage.setItem(this.appStrings.EDU_COMP_LIST, eduCompList);
  }

  static getEduCompList() {
    return Storage.getItem(this.appStrings.EDU_COMP_LIST);
  }

  // Start set & get [EduCompId] ===========================================
  static setEduCompId(EduCompId: number) {
    Storage.setItem(this.appStrings.EDUCOMP_ID, EduCompId);
  }

  static getEduCompId(): number {
    return Number(Storage.getItem(this.appStrings.EDUCOMP_ID));
  }

  // Start set & get [EduCompId] ===========================================
  static setBranchId(BranchId: number) {
    Storage.setItem(this.appStrings.BRANCH_ID, BranchId);
  }

  static getBranchId(): number {
    return Number(Storage.getItem(this.appStrings.BRANCH_ID));
  }

  // Start set & get [UserId] ===========================================
  static setUserId(UserId: number) {
    Storage.setItem(this.appStrings.USER_ID, UserId);
  }

  static getUserId(): number {
    return Number(Storage.getItem(this.appStrings.USER_ID));
  }

  // Start set & get [isUser] ===========================================
  static setIsUserLoggedIn(isLoggedIn: boolean) {
    Storage.setItem(this.appStrings.IsLoggedIn, isLoggedIn);
  }

  static isUserLoggedIn(): boolean {
    return Boolean(Storage.getItem(this.appStrings.IsLoggedIn) ?? false);
  }

  // Start set & get [isUser] ===========================================
  static setCurrentParentChildId(ChildId: number) {
    Storage.setItem(this.appStrings.CURRENT_PARENT_CHILD_ID, ChildId ?? null);
  }

  static getCurrentParentChildId(): number {
    return Number(Storage.getItem(this.appStrings.CURRENT_PARENT_CHILD_ID));
  }
}
