import {HttpClient} from "@angular/common/http";
import {Injectable} from "@angular/core";
import {
  IUserPointsVM,
  giftGalleryViewModel,
} from "app/admin/models/admin/gifts";
import {AppClientService} from "app/shared/services/app-client.service";
import {Subject} from "rxjs";

@Injectable({
  providedIn: "root",
})
export class GiftsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  userPoints = new Subject<IUserPointsVM>();

  getGiftCategoryByID(id: any) {
    return this.get(`Admin/get-gift-category-by-id/${id}`);
  }

  // getGiftByCategoryID(CategoryID: any) {
  //   return this.get(
  //     "Admin/getGiftsBank/" + CategoryID
  //   );
  // }

  RemoveGiftCategory(giftCategoryId: number) {
    return this.get(`Admin/removeGiftCategory/${giftCategoryId}`);
  }

  RemoveGift(giftID: any) {
    return this.get("Admin/removeGift/" + giftID);
  }

  getCategoriesAll<T>(eduCompId: number, page: number) {
    return this.get<T>(`Admin/getGiftsCategoriesAll/${eduCompId}/${page}`);
  }

  getListGifts<T>(eduCompId: number, page: number) {
    return this.get<T>(`Admin/getGiftsAll/${eduCompId}/${page}`);
  }

  getAllGifts<T>(eduCompId: number) {
    return this.get<T>(`Admin/getAllGifts/${eduCompId}`);
  }

  getGiftCategoryDropDown<T>(eduCompId: number) {
    return this.get<T>(`Admin/getAllGiftCategoryDrop/${eduCompId}`);
  }

  getCategoriesSearch(page: number, body: any) {
    return this.post(`Admin/getGiftsCategories/${page}`, body);
  }

  getGiftsSearch(eduCompId: number, page: number, body: any) {
    return this.post(`Admin/getGiftsSearch/${eduCompId}/${page}`, body);
  }

  getCategoryById(Id: any) {
    return this.get("Admin/get-gift-category-by-id/" + Id);
  }

  getGiftGallery() {
    return this.get("Admin/getGiftGallery");
  }

  getGiftHistory() {
    return this.get<giftGalleryViewModel[]>("Admin/getAllUserGifts");
  }

  getPoints() {
    return this.get("Admin/getPointsOfUser").subscribe((res: IUserPointsVM) => {
      this.userPoints.next(res);
    });
  }

  addEditCategory(body: any) {
    return this.post(`Admin/category-add-edit`, body);
  }

  addGift(body: any) {
    return this.post(`Admin/addNewGift`, body);
  }

  getGiftById(id: any) {
    return this.get(`Admin/get-gift-by-Id/${id}`);
  }

  deleteAttach(id: number) {
    return this.get(`Admin/delete-Attach-By-Id/${id}`);
  }

  buyGift(giftId: number) {
    return this.get(`Admin/buyGift/${giftId}`);
  }
}
